import { Component, Input, Output, EventEmitter } from '@angular/core';
import { PriorAuthItem } from '../../../../models/prior-auth.model';
import { CernerActionService } from '../../../../services/cerner-action.service';
import { MessagingService } from '../../../../services/messaging.service';
import { PriorAuthService } from '../../../../services/prior-auth.service';

/**
 * Component for displaying item details and order actions in the milestone details drawer
 */
@Component({
  selector: 'app-item-details',
  templateUrl: './item-details.component.html',
  styleUrls: ['./item-details.component.scss']
})
export class ItemDetailsComponent {
  @Input() worklistItem: PriorAuthItem | null = null;
  @Output() itemRefreshed = new EventEmitter<PriorAuthItem>();

  isLoading = false;

  constructor(
    private cernerActionService: CernerActionService,
    private messagingService: MessagingService,
    private worklistService: PriorAuthService
  ) {}

  /**
   * Get priority color for tags
   */
  getPriorityColor(priority: string): string {
    switch (priority?.toLowerCase()) {
      case 'high':
        return 'red';
      case 'normal':
        return 'blue';
      case 'low':
        return 'green';
      default:
        return 'default';
    }
  }

  /**
   * Modify the order using CernerActionService
   * Builds the required object and calls the MODIFY_ORDER action
   */
  async modifyOrder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for order modification');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for modification');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      // Call the CernerAction service
      await this.cernerActionService.CernerAction('MODIFY_ORDER', actionData);
      
      this.messagingService.success('Opening order modification window...');
      
    } catch (error) {
      this.messagingService.error('Failed to open order modification window');
    }
  }

  /**
   * Cancel the order using CernerActionService
   * Builds the required object and calls the CANCEL_ORDER action
   */
  async cancelOrder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for order cancellation');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for cancellation');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      // Call the CernerAction service
      await this.cernerActionService.CernerAction('CANCEL_ORDER', actionData);
      
      this.messagingService.success('Opening order cancellation window...');
      
    } catch (error) {
      this.messagingService.error('Failed to open order cancellation window');
    }
  }

  /**
   * Cancel and reorder using CernerActionService
   * Builds the required object and calls the CANCEL_REORDER action
   */
  async cancelReorder(): Promise<void> {
    if (!this.worklistItem) {
      this.messagingService.error('No worklist item available for cancel and reorder');
      return;
    }

    if (!this.worklistItem.orderId || this.worklistItem.orderId <= 0) {
      this.messagingService.error('No valid order ID available for cancel and reorder');
      return;
    }

    try {
      // Build the data object for the CernerAction service
      const actionData = {
        orderId: this.worklistItem.orderId,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId || 0,
        // Optional parameters with defaults
        enablePowerPlans: false,
        defaultDisplay: 32, // Default to order profile
        silentSign: false   // Default to normal operation
      };

      // Call the CernerAction service
      await this.cernerActionService.CernerAction('CANCEL_REORDER', actionData);
      
      this.messagingService.success('Opening cancel and reorder window...');
      
    } catch (error) {
      this.messagingService.error('Failed to open cancel and reorder window');
    }
  }

  /**
   * Reprocess COMPASS for the current worklist item by calling the backend service.
   * Sends requestType 'reprocessCompass' and the compass_worklist_id as requestId.
   */
  reprocessCompass(): void {
    if (!this.worklistItem) return;
    const worklistId = this.worklistItem.compass_worklist_id;
    this.isLoading = true;
    this.worklistService.reprocessCompass(worklistId).subscribe({
      next: (result) => {
        this.messagingService.success('COMPASS reprocessing triggered successfully.');
        // Emit event to refresh the worklist item after reprocessing
        this.itemRefreshed.emit(this.worklistItem!);
      },
      error: (err) => {
        this.messagingService.error('Failed to reprocess COMPASS.');
      },
      complete: () => {
        this.isLoading = false;
      }
    });
  }
} 